<template>
  <div id="app">
    <div class="sidebar">
    <a href="https://zhih.me" title="底噪" class="logo">
      <img src="../static/icons/logo.png" alt="底噪">
    </a>
      <div class="sidebar-wapper">
        <template v-for="(menu, index) in sidebar">
          <router-link :to="menu.link" :title="menu.name" class="menu" :key="index">
            <div class="menu-cursor" v-html="menu.icon"></div>
            <span>{{ menu.name }}</span>
          </router-link>
        </template>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>

  import icons from '../static/icons/icon.json'

  export default {
    name: 'App',
    data () {
      return {
        sidebar: icons
      }
    }
  }
</script>

