<template>
	<div class="page-wrapper">
		<div class="header">
			<div class="title">macOS</div>
			<!-- <a href="https://zhih.me" class="submit">Submit</a> -->
		</div>
		<div class="wrapper os-wrapper">
			<a class="os-card shadow" v-for="(os, index) in OSimages" :key="index" target="_blank" :title="os.OS" :href="os.Link">
				<div class="os-icon">
					<img v-lazy="(os.OS.substr(0, 5) === '10.13') ? OSicons[0] : OSicons[1]" :alt="os.OS">
				</div>
				<div class="os-detail">
					<div class="os-name">{{ os.OS }}</div>
					<li>{{ os.Version }}</li>
					<li>{{ os.Author }}</li>
				</div>
			</a>
		</div>
	</div>
</template>

<script>
	import OSimages from '../../static/data/OS-images.json'

	export default {
		name: 'OSimages',
		data () {
			return {
				OSimages: OSimages,
				OSicons: {
					0: '/hackintosh/static/img/OS-images/10.13.png',
					1: '/hackintosh/static/img/OS-images/10.14.png'
				}
			}
		}
	}
</script>

<style src="../assets/style.css"></style>



