<template>
	<div class="page-wrapper">
		<div class="header">
			<div class="title">Clover Themes</div>
			<!-- <a href="https://zhih.me" class="submit">Submit</a> -->
		</div>
		<div class="wrapper t-center">
			<a class="theme-card shadow" v-for="(theme, index) in themes" :key="index" target="_blank" :title="theme.Name" :href="theme.Link">
				<img v-lazy="theme.Screenshot" :alt="theme.Name">
				<span>{{ theme.Name }}</span>
			</a>
		</div>
	</div>
</template>

<script>
	import themes from '../../static/data/themes.json'

	export default {
		name: 'Themes',
		data () {
			return {
				themes: themes
			}
		}
	}
</script>

<style src="../assets/style.css"></style>



