<template>
  <div class="page-wrapper">
    <div class="header">
      <div class="title">Hackintosh Desktop EFI</div>
      <!-- <a href="https://zhih.me" class="submit">Submit</a> -->
    </div>
    <div class="wrapper">
      <div class="card shadow" v-for="(items, key, index) in desktop" :key="index">
        <div class="model">{{ items.Motherboard }}</div>
        <div class="details">
          <li>CPU：{{ items.CPU }}</li>
          <li>GPU：{{ items.Graphics }}</li>
          <li>声卡：{{ items.Audio }}</li>
          <li>网卡：{{ items.Network }}</li>
          <li>OS：&nbsp; {{ items.OS }}</li>
        </div>
        <div class="card-bottom">
          <div class="author">
            <span>{{ items.Author.substr(0,1).toUpperCase() }}</span>
            {{ items.Author }}
          </div>
          <a class="get-btn" :href="items.Link" target="_blank" title="get efi">
            <img src="../../static/icons/download.svg" alt="download">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
	import desktop from '../../static/data/desktop.json'

	export default {
		name: 'Desktop',
		data () {
			return {
				desktop: desktop
			}
		}
	}
</script>

<style src="../assets/hackintosh.css"></style>



