import Vue from 'vue'
import Router from 'vue-router'
import Laptop from '@/components/Laptop'
import Freeapp from '@/components/Freeapp'
import Desktop from '@/components/Desktop'
import OSimages from '@/components/OS-images'
import themes from '@/components/Themes'
import Kext from '@/components/Kext.vue'

Vue.use(Router)

export default new Router({
	base: '/hackintosh/',
	routes: [
	{
		path: '/',
		name: 'Laptop',
		component: Laptop
	},
	{
		path: '/freeapp',
		name: 'Freeapp',
		component: Freeapp
	},
	{
		path: '/desktop',
		name: 'Desktop',
		component: Desktop
	},
	{
		path: '/OS-images',
		name: 'OSimages',
		component: OSimages
	},
	{
		path: '/themes',
		name: 'Themes',
		component: themes
	},
	{
		path: '/kext',
		name: 'Kext',
		component: Kext
	}
	]
})



